
.navbar{
    height: 4rem;

    &.big-nav{
        height: 6rem;
    }

    &.bg-color-black{
        background-color: rgb(0, 0, 0);
        color: white;
    }

    &.bg-color-white{
        background-color: rgb(255, 255, 255);
    }

    &.bg-color-light{
        background-color: rgb(243, 234, 234);
        color: white;
    }

    &.bg-color-green{
        background-color: rgb(22, 80, 14);
    }

    &.bg-color-blue{
        background-color: rgb(14, 78, 216);
    }

    &.bg-color-yellow{
        background-color: rgb(226, 230, 18);
    }

    &.bg-color-pink{
        background-color: rgb(255,105,180);
    }

    &.bg-color-dark-blue{
        background-color: rgb(96, 3, 245);
        color: white;
    }

    &.bg-color-grey{
        background-color: rgb(51, 46, 46);
        color: white;
    }
}